import Vue from 'vue'
import VueRouter from 'vue-router'
import {getComponentByRootPath} from './util'

Vue.use(VueRouter)

import businessNav from './businessNav';
import commodityTrace from './commodityTrace';
import traceCodeManage from './traceCodeManage';
import regulation from './regulation';

let getComponent;

//开发环境下不使用按需加载打包模式,提升打包速度
getComponent = getComponentByRootPath('');
const root = {
    path: '/', // 登录页
    name: '',
    redirect: {path: '/main'}
};
const appRoutes = {
    path: '/main',
    name: '首页',
    component: getComponent('layout/Main'),
    children: [
        ...businessNav,
        ...commodityTrace,
        ...traceCodeManage,
        ...regulation
    ]
}
const login = {
    path: '/login',
    name: '登录',
    component: getComponent('login/Index'),
};

//未定义路由跳转404
const notFindPage = {
    path: '*',
    name: '找不到页面',
    component: getComponent('notFind/Index')
};


const router = new VueRouter({
    routes: [
        root,
        appRoutes,
        login,
        notFindPage
    ]
});
router.beforeEach((to, from, next) => {
    let el: HTMLElement = document.querySelector('.el-main') as HTMLElement;
    if (el) {
        el.scrollTop = 0;
    }
    next();
});

const appRouter = appRoutes.children

export {
    appRouter
}

export default router
