import {getComponentByRootPath} from './util'

const rootPath = '/regulation';
let getComponent;

//开发环境下不使用按需加载打包模式,提升打包速度
getComponent = getComponentByRootPath(rootPath);
export default [
    {
        path: rootPath,
        name: '政策法规 ',
        icon: 'icon-uniE059',
        meta: {
            isMenu: true,
        },
        component: getComponent('Index')
    }
];